___CSS_LOADER_EXPORT___.push([module.id, `*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-size: 10px;
}

@media only screen and (min-width: 35.5em) {
    html {
        font-size: 12px;
    }
}

@media only screen and (min-width: 48em) {
    html {
        font-size: 14px;
    }
}

@media only screen and (min-width: 64em) {
    html {
        font-size: 16px;
    }
}

@media only screen and (min-width: 80em) {
    html {
        font-size: 18px;
    }
}

@media only screen and (min-width: 120em) {
    html {
        font-size: 20px;
    }
}

@media only screen and (min-width: 160em) {
    html {
        font-size: 22px;
    }
}

@media only screen and (min-width: 240em) {
    html {
        font-size: 24px;
    }
}

body {
    display: flex;
    height: 100vh;
    align-items: center;
    flex-direction: column;
    background-color: rgba(15, 131, 102, 0.39);
}

#weatherDiv {
    flex: 1;
    padding: 2rem;
    width: 100%;

    display: grid;
    grid-template-columns: repeat(3, minmax(10rem, 1fr));
    grid-template-rows: repeat(2, minmax(10rem, 1fr));
    align-items: center;
    justify-content: center;
}

#todayWeatherDiv,
#tomorrowWeatherDiv,
#overmorrowWeatherDiv {
    font-size: 1rem;

    display: flex;
    flex-direction: column;

    align-items: center;
    justify-content: center;
}

#todayWeatherDiv {
    grid-column: 1 / 2;
    grid-row: 1 / 2;
}

#tomorrowWeatherDiv {
    grid-column: 2 / 3;
    grid-row: 1 / 2;
}

#overmorrowWeatherDiv {
    grid-column: 3 / 4;
    grid-row: 1 / 2;
}

.weatherTitle {
    font-weight: bold;
    font-size: 1.1rem;
}

.weatherCondition {
    display: flex;
    align-items: center;
    justify-content: center;
}

#locationWeatherDiv {
    grid-column: span 3;
    grid-row: 2 / 3;

    display: flex;
    justify-content: center;
}

#weatherLocation {
    font-size: 1.2rem;
}

#formDiv {
    flex: 1;
}

#locationForm {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

#locationInput, #formButton {
    border-radius: 10rem;
    padding: 1rem;
    font-size: 1.1rem;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;;;IAGI,SAAS;IACT,UAAU;IACV,sBAAsB;AAC1B;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI;QACI,eAAe;IACnB;AACJ;;AAEA;IACI;QACI,eAAe;IACnB;AACJ;;AAEA;IACI;QACI,eAAe;IACnB;AACJ;;AAEA;IACI;QACI,eAAe;IACnB;AACJ;;AAEA;IACI;QACI,eAAe;IACnB;AACJ;;AAEA;IACI;QACI,eAAe;IACnB;AACJ;;AAEA;IACI;QACI,eAAe;IACnB;AACJ;;AAEA;IACI,aAAa;IACb,aAAa;IACb,mBAAmB;IACnB,sBAAsB;IACtB,0CAA0C;AAC9C;;AAEA;IACI,OAAO;IACP,aAAa;IACb,WAAW;;IAEX,aAAa;IACb,oDAAoD;IACpD,iDAAiD;IACjD,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;;;IAGI,eAAe;;IAEf,aAAa;IACb,sBAAsB;;IAEtB,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;IACI,kBAAkB;IAClB,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,eAAe;AACnB;;AAEA;IACI,iBAAiB;IACjB,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;IACI,mBAAmB;IACnB,eAAe;;IAEf,aAAa;IACb,uBAAuB;AAC3B;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,OAAO;AACX;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,oBAAoB;IACpB,aAAa;IACb,iBAAiB;AACrB","sourcesContent":["*,\n*::before,\n*::after {\n    margin: 0;\n    padding: 0;\n    box-sizing: border-box;\n}\n\nhtml {\n    font-size: 10px;\n}\n\n@media only screen and (min-width: 35.5em) {\n    html {\n        font-size: 12px;\n    }\n}\n\n@media only screen and (min-width: 48em) {\n    html {\n        font-size: 14px;\n    }\n}\n\n@media only screen and (min-width: 64em) {\n    html {\n        font-size: 16px;\n    }\n}\n\n@media only screen and (min-width: 80em) {\n    html {\n        font-size: 18px;\n    }\n}\n\n@media only screen and (min-width: 120em) {\n    html {\n        font-size: 20px;\n    }\n}\n\n@media only screen and (min-width: 160em) {\n    html {\n        font-size: 22px;\n    }\n}\n\n@media only screen and (min-width: 240em) {\n    html {\n        font-size: 24px;\n    }\n}\n\nbody {\n    display: flex;\n    height: 100vh;\n    align-items: center;\n    flex-direction: column;\n    background-color: rgba(15, 131, 102, 0.39);\n}\n\n#weatherDiv {\n    flex: